
import Vue from 'vue';

export const enum Levels {
    INFO = 'info',
    WARNING = 'warning',
    ERROR = 'error'
}

export class KiwiError extends Error {
    level: string;
  
    constructor({ level = Levels.ERROR, message = "Ein unbekannter Fehler ist aufgetreten, bitte den Administrator informieren und/oder spter noch einmal probieren." }: { level?: string; message?: string }) {
		// bergibt die verbleibenden Parameter (einschlielich Vendor spezifischer Parameter) dem Error Konstruktor
		super(message);
	
		// Behlt den richtigen Stack-Trace fr die Stelle bei, an der unser Fehler ausgelst wurde
        this.stack = new Error().stack;
	
		// Benutzerdefinierte Informationen
		this.level = level;
		this.message = message;
    }
}

export class ThreadErrorHandler {

    static handleError(context: Vue, error: KiwiError): void {
        console.error("Error: '" + error.message);
        context.$store.dispatch("snackbar/showMessage", {
            message: "Ein unbekannter Fehler ist aufgetreten, bitte den Administrator informieren und/oder spter noch einmal probieren. <br>" + 
                     "An unknown error has occurred, please inform the administrator and/or try again later.",
            level: error.level
        });
    }

}