import { Component, Vue } from "vue-property-decorator";

/**
 * Mit dem SaveLeave Mixin kann ein Datenverlust durch ungewolltest Navigieren verhindert werden.
 *
 * Das Mixin lsst sich ber die Mixins Funktion vom `vue-property-decorator` einbinden.
 *
 * Anschlieend kann mittels des berschreibens der `isDirty()` Methode festgelegt werden,
 * ob sicher navigiert werden kann oder eine Rckfrage an den Nutzer stattfinden soll.
 * Diese Rckfrage kann z.B. ber einen Dialog gelst werden. Hierzu bietet das SaveLeaveMixin
 * ein `saveLeaveDialog` Flag an. Fr genererische Dialoge bietet das SaveLeaveMixin bereits Titel und
 * Text mit an.
 *
 * Mit dem Aufruf von `leave()` oder `cancel()` kann die Entscheidung des Nutzers ausgefhrt werden.
 */
Component.registerHooks([
    'beforeRouteLeave',
]);
@Component
export default class SaveLeaveMixin extends Vue{
    name = "saveLeaveMixin";


    saveLeaveDialogTitle = 'Ungespeicherte nderungen';
    saveLeaveDialogText = 'Es sind ungespeicherte nderungen vorhanden. Wollen Sie die Seite verlassen?';
    saveLeaveDialog = false;
    isSave = false;
    // eslint-disable-next-line
    next: any = null;

    // eslint-disable-next-line
    beforeRouteLeave (to: any, from: any, next: any): void {
        if(this.isDirty() && !this.isSave) {
            this.saveLeaveDialog = true;
            this.next = next;
        } else {
            this.saveLeaveDialog = false;
            next();
        }
    }

    cancel(): void {
        this.saveLeaveDialog = false;
        this.next(false);
    }

    leave(): void {
        this.next();
    }

    /**
     * Methode sollte berschrieben werden.
     * In dieser Methode wird geprft, ob der Nutzer ungespeicherte nderungen eingegeben hat.
     */
    isDirty(): boolean {
        return true;
    }

}
